<template>
    <div class="first-layout">
        <div class="word">
            <div id="resultContent"/>
            <div id="resultContent1"/>
            <div id="resultContent2"/>
        </div>
        <button class="word-export" @click="ExportWord">Word</button>
    </div>
</template>

<script>
//

export default {
    name: "Result",
    data() {
        return {
            preview: require('docx-preview'),
            template: 'document/.docx',
            template1: [
                'document/96-100.docx',
                'document/90-95.docx',
                'document/80-89.docx',
                'document/70-79.docx',
                'document/60-69.docx',
                'document/30-59.docx',
                'document/30.docx',
            ],
            template2: [
                'document/96-100.docx',
                'document/90-95.docx',
                'document/80-89.docx',
                'document/70-79.docx',
                'document/60-69.docx',
                'document/30-59.docx',
                'document/30.docx',
            ],
            FinishedNumber: 0,
        };
    },
    mounted() {
        this.FinishedNumber = 0;
        let score = this.$store.state.score;
        let options = {
            className: "docx", ///
            inWrapper: true, //
            ignoreWidth: false, //
            ignoreHeight: false, //
            ignoreFonts: false, //
            renderEndnotes: true,
            renderFooters: true,
            renderFootnotes: true,
            renderHeaders: true,
            breakPages: true, //
            ignoreLastRenderedPageBreak: true, //
            experimental: true, //()
            trimXmlDeclaration: true, //XMLXML
            useBase64URL: true, //base 64 URLURLcreateObjectURL
            useMathMLPolyfill: true, //includes MathML polyfills for chrome, edge, etc.
            debug: false, //
        };
        let promise = this.ShowWord(score, 'resultContent', options).then(this.setWordData);
        if (this.$store.state.didExp) {
            promise
                .then(() => this.ShowWord(score, 'resultContent1', options))
                .then(() => this.ShowWord(score, 'resultContent2', options))
                .then(() => setTimeout(this.MergeWord, 100));
        }
    },
    methods: {
        //
        GetLevel(score) {
            return score >= 96 ? 0
                : score >= 90 ? 1
                    : score >= 80 ? 2
                        : score >= 70 ? 3
                            : score >= 60 ? 4
                                : score >= 30 ? 5
                                    : 6;
        },
        //Http
        NewHttpRequest(url) {
            let XML = new XMLHttpRequest();
            XML.open('get', url, true);
            XML.setRequestHeader("Content-type", 'application/json');
            XML.responseType = 'arraybuffer';
            XML.send();
            return XML;
        },
        //
        async ShowWord(score, id, options) {
            let data = null;
            let template = this.template;
            if (id.includes('1')) {
                let level = this.GetLevel(score);
                template = this.template1[level];
            }
            else if (id.includes('2')) {
                let level = this.GetLevel(score);
                template = this.template2[level];
            }
            let promise = new Promise((resolve, reject) => {
                let XML = this.NewHttpRequest(`${template}`);
                XML.onload = result => {
                    data = result.target.response;
                    this.preview
                        .renderAsync(data, document.getElementById(id), null, options)
                        .then(result => {
                            this.FinishedNumber += 1;
                            resolve('');
                            //console.log(result);
                        })
                        .catch(() => {
                            reject();
                        });
                };
                XML.onerror = () => reject('');
            });
            await promise;
        },
        //Word
        MergeWord() {
            if (this.FinishedNumber < 3) {
                setTimeout(this.MergeWord, 100);
                return;
            }
            let article = document.getElementById('resultContent').getElementsByTagName('article')[0];
            let e = document.getElementById('resultContent1');
            let children = e.getElementsByTagName('article')[0].children;
            for (let child of children) {
                article.appendChild(child);
            }
            e.remove();
            e = document.getElementById('resultContent2');
            children = e.getElementsByTagName('article')[0].children;
            for (let child of children) {
                article.appendChild(child);
            }
            e.remove();
        },
        //Word
        setWordData() {
            let result = document.getElementById('resultContent');
            if (!result) return;
            let line = result.getElementsByTagName('article')[0].getElementsByTagName('p')[1];
            let node = line.childNodes[8];
            node.style.minHeight = '12pt';
            node.style.fontSize = '12pt';
            node.style.textDecoration = 'underline';
            //
            node = line.children[12];
            node.textContent = new Date().toLocaleDateString();
            //
            node = line.children[17];
            node.textContent = this.$store.state.score;
            node = line.children[13].cloneNode(true);
            line.appendChild(node);
        },
        //Word
        ExportWord() {
            let FileSaver = require('file-saver');
            let htmlDocx = require('html-docx-js/dist/html-docx');
            let html = document.getElementById('resultContent').innerHTML;
            let content = `
            <!DOCTYPE html>
    	 	<html lang="zh">
	            <body style="font-family: Times New Roman,serif; font-weight: bold; color: rgb(0, 0, 0); min-height: 12pt; font-size: 12pt;">
	                ${html}
	            </body>
            </html>`;
            let converted = htmlDocx.asBlob(content);
            FileSaver.saveAs(converted, '.docx');
        }
    }
};
</script>

<style scoped>

</style>